body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-title {
  margin: 5px 5px;
}

.header-bar{
  -webkit-align-items: center;
          align-items: center;
  padding-top: 20px;
  margin-left: 41%;
  -webkit-justify-content: center;
          justify-content: center;
  display: grid;
  grid-auto-flow: column;
  grid-column-gap: 60%;
}

#url {
  width: 300px;
}

.container-wrapper {
  max-width: 80%;
  margin: auto;
  display: grid;
  grid-auto-flow: row;
}

.container-wrapper > div {
  padding: 1px 0px;
  margin-top: 1px;
}

.container-video {
  position: relative;
  max-width: 640px;
  width: 80%;
  max-height: 390px;
  height: 100%;
  margin: auto;
}

.overflow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  opacity: 0.6;
  z-index: -16;
}

.side1 {
  position: relative;
}

.container-video div:not(.overflow) {
  z-index: 1;
}

.progressBar {
  position: relative;
}

.bookmark-tip {
  position: absolute;
  width: 3px;
  height: 1.75em;
  background: red;
  z-index: 1000;
}

/* Important part */
.modal-dialog {
  overflow-y: visible !important;
  overflow-y: initial !important
}

.modal-body {
  height: 400px;
  overflow-y: auto;
}

.center-me {
  margin: 0 auto;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  vertical-align: center;
}

.link {
  padding-top: 100px;
  padding-bottom: 100px;
}

.mytext {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

.readme {
  margin-left: 10px;
  padding-bottom: 10px;
  font-size: calc(10px + 1vmin);
}

.drawerPaper {
  -webkit-align-items: 'left';
          align-items: 'left';
  white-space: 'nowrap';
  width: 280px;
  /* transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp;
    duration: theme.transitions.duration.enteringScreen;
  }); */
}

.drawerPaperClose {
  overflow-x: 'hidden';
  /* transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }) */
  /* width: theme.spacing.unit * 7; */
  /* [theme.breakpoints.up('sm')]: {
    width: theme.spacing.unit * 9,
  } */
}

.drawer {
  font-size: 300%;
}

.preview {
  width: 50%;
  height: 50%
}

#title {
  text-align: center;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

#navigations {
  margin: 10%;
  margin-top: 30px;
  text-align: center;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  border-collapse: collapse;
  border: 3px solid #ddd;
  width: 80%;
}

#navigations td, #navigations th {
  border: 1px solid #ddd;
  padding: 8px;
}

#navigations tr:nth-child(even){background-color: #f2f2f2;}

#navigations tr:hover {background-color: #ddd;}

#navigations th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #282c34;
  color: white;
}


